import React from 'react';

import axios from "axios";

import { API_URL_USER_ITEMS } from "../constants";
import { USER_ID } from "../constants"

import sidor from "./img/inventari.png"

import setc from "./img/fragment(setca).png"

import Item from './Item';


class Bag extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            Useritems:[],
        };
    };
    
    componentDidMount() {
        this.resgetUserItems()
    };

    getUserItems(){
        axios.get(API_URL_USER_ITEMS + USER_ID).then(res => this.setState({ Useritems: res.data }));
    };

    resgetUserItems(){
        this.getUserItems();
    }
  render(){

    const rows = [];
    for (let i = 0; i < Math.floor(this.state.Useritems.length/3) + 1; i++) {
        rows.push(<img src={setc} className="setcimg" alt="Секундочку"/>);
    }

    console.log(this.state.Useritems[0])
    return(
        <main>
                <div class="torg">
                    <img src={sidor}  alt="Секундочку" className='urur'/>
                </div>
                {!this.state.Useritems || this.state.Useritems <= 0 ? (
                    <div class="setcatorgrt">
                        <img src={setc} alt="Секундочку" className="setcimg"/>
                    </div>
                ):(
                    <>
                    <div class="setcatorgrt">
                        { Math.floor(this.state.Useritems.length/3) !== 0 ? (
                            <>
                                {rows}
                            </>
                        ) : (
                            <img src={setc} alt="Секундочку" className="setcimg"/>
                        )}
                    </div>
                    <div className='blockbag'>
                        {this.state.Useritems.map(ite => (
                            <div className='item'>
                                <div className='photo'>
                                    <Item create={true} resetState={this.resetState} item = {ite} wiev = {false} getUserItems={this.getUserItems}/>
                                </div>
                            </div>
                        ))}
                    </div>
                    </>
                )}
                
            </main>
    )
  }
}

export default Bag