import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import Shop from './Shop';
import Bag from './Bag';

import torgOn from "./img/torg(on).png"
import torgOff from './img/torgOff.png'
import reating from "./img/reatingOff.png"
import fqa from "./img/fqaOff.png"
import puts from "./img/pust.png"
import bagOff from './img/bagOff.png'
import bagOn from './img/bag(on).png'
let img1 = bagOff;
let img2 = torgOn;
function Menu(props){
    const [click, setClick] = useState("Shop")

    useEffect(()=>{
        
        if (click === "Shop"){ ReactDOM.render(<Shop />,document.getElementById('but')); img1 = bagOn; img2 = torgOff}
        if (click === "Bag"){ ReactDOM.render(<Bag />, document.getElementById('but')); img1 = bagOff; img2 = torgOn}
    })

    return(
        <div class="menu">
          <img src={img1} className="aa" onClick={ () => setClick("Bag")} alt="Секундочку"/>
          <img src={reating} className="aa" alt="Секундочку"/>
          <img src={img2} className="aa" onClick={ () => setClick("Shop")} alt="Секундочку"/>
          <img src={fqa} className="aa" alt="Секундочку"/>
          <img src={puts} className="aa" alt="Секундочку"/>
        </div>
    )
}

export default Menu