import React from 'react';

import axios from "axios";

import { API_URL_ITEMS } from "../constants";
import Item from './Item';

import sidor from "./img/torg.png"

import setc from "./img/fragment(setca).png"



class Shop extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            items:[],
            cl : true,
        };
    };
    componentDidMount() {
        this.resgetItem()
    };

    getItem(){
        axios.get(API_URL_ITEMS).then(res => this.setState({ items: res.data }));
    };

    resgetItem(){
        this.getItem();
    }

    render(){

        const rows = [];
        for (let i = 0; i < Math.floor(this.state.items.length/3) + 1; i++) {
            rows.push(<img src={setc} className="setcimg2" alt="Секундочку"/>);
        }
        
        return(
            <main>
                <div class="torg">
                    <img src={sidor} alt="Секундочку" className='torgimg'/>
                </div>
                {!this.state.items || this.state.items <= 0 ? (
                    <img src={setc} alt="Секундочку" className="setcimg1"/>
                ):(
                    <>
                    <div class="setcatorg">
                        { Math.floor(this.state.items.length/3) !== 0 ? (
                            <>
                                {rows}
                            </>
                        ) : (
                            <img src={setc} alt="Секундочку" className="setcimg2"/>
                        )}
                    </div>
                    <div className='blockshop'>
                        {this.state.items.map(ite => (
                            <div className='item'>
                                <div className='photo'>
                                    <Item create={true} resetState={this.resetState} item = {ite} wiev = {true}/>
                                </div>
                            </div>
                        ))}
                    </div>
                    </>
                )}
                
            </main>
        )
  }
}

export default Shop