import React from 'react';
import CopyText from 'react-copy-text'

import us from './img/grap.png';
import nameus from './img/buttonName.png';
import ref from "./img/buttonRefLink.png"

class UserHeader extends React.Component{

    state  = {textToCopy: ""}

    onButtonClick = () => this.setState({ textToCopy: 'https://t.me/Strashnoe_delo_bot?start=' + this.props.user.userid })
    onCopied = (text) => console.log(`${text} was copied to the clipboard`)

  render(){
    const user = this.props.user
    return(
        <div className="user">
            <img src={us} className="grap" alt='сек'/>
            <div class="but">
              <img src={nameus} className="aaa" alt='сек'/>
              <img src={ref} className="aaa" onClick={this.onButtonClick} alt='сек'/>
                <CopyText text={this.state.textToCopy} onCopied={this.onCopied} />
            </div>
            <div className='user_names'>
              <h5>{user.name_user}</h5>
            </div>
            <div className="statics">
              {(user.souls<0)?(
                <div className="ss">{('-000'+Math.abs(user.souls)).slice(-4)}</div>
              ):(
                <div className="ss">{('0000'+user.souls).slice(-4)}</div>
              )}
              
              <div className="ss">{('0000'+user.ref).slice(-4)}</div>
            </div>
        </div>
        )
    }
}

export default UserHeader