import React, { Component, Fragment } from "react";
import { Button, Modal, Form } from "reactstrap";
import ReactDOM from 'react-dom';

import axios from "axios";

import { API_URL } from "../constants";
import { API_URL_USER_ITEMS } from "../constants";
import { API_URL_USER_ITEMS_DEL } from "../constants";
import { API_USER_PUT } from "../constants";
import { USER_ID } from "../constants";

import backbutton from "./img/back(item).png"
import buybutton from "./img/buyitem.png"
import openIt from "./img/openitem.png"
import niz from "./img/nizItem.png"
import trat from "./img/trat(item).png"
import yes_but from "./img/yes(item).png";
import no_but from "./img/otmen(item).png";



class Item extends Component {
  state = {
    pk: 0,
    userId: 0,
    price: 0,
    info: "",
    name: "",
    imgs: "",
    modal: false,
    wiev: false,
    user: [],
    banerOne: false,
    banerTwo: false,
  };

  componentDidMount() {
    if (this.props.item) {
      axios.get(API_URL + USER_ID).then(res => this.setState({ user: res.data }));
      const { pk, price, info, name, img } = this.props.item;
      const userId = USER_ID;
      this.setState({ pk:pk, userId:userId, price:price, info:info, name:name, wiev : this.props.wiev, imgs: img});
    }
  }

  editUser = e => {
    e.preventDefault();
    if (this.state.user.refid == null){
      this.state.user.refid = -1;
    }
    axios.put(API_USER_PUT + this.state.user.pk, { pk : this.state.user.pk, userid : this.state.user.userid, refid : this.state.user.refid, souls : this.state.user.souls - this.state.price, ref : this.state.user.ref, name_user:this.state.user.name_user}).then(() => {
      this.props.resetState();
    });
    document.location.reload();
  };

  toggle = () => {
    this.setState(previous => ({
      modal: !previous.modal,
      banerOne: false,
      banerTwo: false,
    }));
    this.resetState();
  };

  baner_One = item => {
    this.setState(previous => ({
      banerOne: !previous.banerOne
    }));
    ReactDOM.render(<h3 id="itemimgs">Вы бы хотели купить { item.name } за { item.price } душ</h3>,document.getElementById('itemimgs'));
    this.resetState();
  };
  baner_Two = item =>{
    this.setState(previous => ({
      banerTwo: !previous.banerTwo
    }));
    ReactDOM.render(<h3 id="itemimgs">Вы бы хотели потратить { item.name }</h3>,document.getElementById('itemimgs'));
    this.resetState();
  }

  itempost = e => {
    e.preventDefault();
    console.log(this.state.imgs)
    if (this.state.user.souls >= this.state.price) {
      this.editUser(e)
      axios.post(API_URL_USER_ITEMS, { pk : this.state.pk, userId : this.state.userId, price : this.state.price, info : this.state.info, name : this.state.name, img : this.state.imgs}).then(() => {
        this.resetState();
      });
      this.toggle();
      }
  };

  itemdel = pk => {
    axios.delete(API_URL_USER_ITEMS_DEL + pk).then(() => {
      this.props.resgetUserItems();
    });
    document.location.reload();
    this.toggle();
  };

  render() {
    const create = this.props.create;
    const item = this.props.item;
    var button = <Button onClick={this.toggle}>Edit</Button>;
    if (create) {
      button = (
        <img src={ item.img } alt="Секундочку" className="imgTavar" onClick={this.toggle}/>
      );
    }

    return (
      <Fragment style={{display:"flex", justifyContent: "center", alignItems: "center"}}>
        {button}
        <Modal isOpen={this.state.modal} toggle={this.toggle} style={{ top: "0px", margin: "auto", background: "rgba(0, 0, 0, 0.5)",  width: "390px", outline: 'none'}}>
          {this.state.wiev === true ? (
            <div className="item_view" style={{outline:'none'}}>
              <Form>
                  <img src={ openIt } alt="Секундочку" className="openIt"/>
                  <div className="imgItem" id="itemimgs">
                    <img src={ item.img } alt="Секундочку" className="imgTavar" id="itemimgs"/>
                  </div>
                  <div className="infoItem">
                    <span>{item.info}</span>
                  </div>
                  <div className="butitems">
                    {(!this.state.banerOne)?(
                      <>
                        <img src={ buybutton } alt= "Секундочку" onClick={() => this.baner_One(item) } className = "butit"/>
                        <img src={ backbutton } alt= "Секундочку" onClick={ this.toggle } className = "butit"/>
                      </>
                    ):(
                      <>
                        <img src={ yes_but } alt= "Секундочку" onClick={ this.itempost } className = "butit"/>
                        <img src={ no_but } alt= "Секундочку" onClick={ this.toggle } className = "butit"/>
                      </>
                    )}
                  </div>
                  <img src={niz} alt="Секундочку" className="niz"/>
              </Form>
            </div>
          ):(
          <div className="item_view" style={{outline:'none'}}>
            <Form >
                <img src={ openIt } alt="Секундочку" className="openIt"/>
                <div className="imgItem" id="itemimgs">
                    <img src={ item.img } alt="Секундочку" className="imgTavar" id="itemimgs"/>
                </div>
                <div className="infoItem">
                  <span>{item.info}</span>
                </div>
                <div className="butitems">
                  {(!this.state.banerTwo)?(
                    <>
                      <img src={ trat } alt= "Секундочку" onClick={() =>this.baner_Two(item)} className = "butit"/>
                      <img src={ backbutton } alt= "Секундочку" onClick={this.toggle} className = "butit"/>
                    </>
                  ):(
                    <>
                      <img src={ yes_but } alt= "Секундочку" onClick={() => this.itemdel(item.pk)} className = "butit"/>
                      <img src={ no_but } alt= "Секундочку" className = "butit" onClick={this.toggle}/>
                    </>
                  )}
                </div>
                <img src={niz} alt="Секундочку" className="niz"/>
            </Form>
          </div>
          )}
            
        </Modal>
      </Fragment>
    );
  }
}

export default Item;